.card {
  box-sizing: border-box;
  font-family: Roboto, serif;
  width: 200px;
  height: 408px;
  border: 1px #f3f3f3 solid;
  border-radius: 5px;

  &__img {
    width: 160px;
    height: 134px;
    padding: 32px 19px 0;
  }

  &__link {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    width: 166px;
    height: 36px;
    padding: 36px 16px 0;
    color: var(--color-text);
  }
  
  &__code {
    font-size: 10px;
    font-weight: 400;
    color: var(--color-transparent);
    width: 98px;
    height: 14px;
    padding: 4px 0 0 16px;
  }
  
  &__grade {
    display: flex;
    justify-content: space-between;
    width: 166px;
    height: 16px;
    padding: 16px 16px 0;
  }
  
  &__grade-reviews {
    display: flex;
    align-items: flex-end;
    text-align: right;
    font-size: 10px;
    line-height: 14px;
    font-weight: 400;
    color: var(--color-text);
    padding-bottom: 1px;
  }
  
  &__price-container {
    display: flex;
    justify-content: space-between;
    width: 166px;
    height: 16px;
    padding: 24px 16px 0;
  }
  
  &__price-text-paragraf {
    margin: 0;
    padding: 0;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: var(--color-transparent);
  }
  
  &__price-paragraf {
    margin: 0;
    padding: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    color: var(--color-text);
  }
  
  &__button {
    padding: 18px 16px;
  }
  
  &__buy {
    padding: 18px;
  }
  
  &__buy-link {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
  
    width: 166px;
    height: 40px;
    color: #fff;
    font-size: 14px;
    line-height: 16px;
    background-color: var(--color-buy);
    font-weight: 700;
    border: 1px solid var(--color-buy);
    border-radius: 5px;
    text-decoration: none;
    text-transform: uppercase;

    &:hover {
      color: var(--color-buy);
      background-color: #fff;
    }
  }

}
