.stars {
  display: flex;

  &__star {
    display: flex;
    background-image: url("/images/star.svg");
    background-repeat: no-repeat;
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-position: 50%;
  }
  
  &--1 :nth-child(-n + 1),
  &--2 :nth-child(-n + 2),
  &--3 :nth-child(-n + 3),
  &--4 :nth-child(-n + 4),
  &--5 :nth-child(-n + 5) {
    background-image: url("/images/star-active.svg");
  }
}
